  /* Radio Classes */
.myinput[type="radio"]:before{
    position: relative;
    display: block;
    width:  11px;
    height: 11px;
    -moz-border-radius: 50px;
    -webkit-border-radius: 50px;
    border-radius: 100%;
    content: "";
    /*background: #FFF;*/
}
.myinput[type="radio"]:after{
    position: relative;
    display: block;
    left: 0px;
    top:  0px;
    width: 7px;
    height: 7px;
    -moz-border-radius: 50px;
    -webkit-border-radius: 50px;
    border: 5px solid #AAAAAA;
    border-radius: 100%;
    content: "";
    background-image: linear-gradient(135deg, #FFF 0%,#FFF 100%);
    background-repeat: no-repeat;
    background-position:center;
}

.myinput[type="radio"]:not(:disabled):hover:after{
    background-image: linear-gradient(135deg, #8BB0C2 0%,#FFF 100%);
    border-color: #2196F3;
}
.myinput[type="radio"]:not(:disabled):hover:before{
    border-color: #3D7591;
}
/* Large checkboxes */
.myinput.large{
    height:30px;
    width: 30px;
}

.myinput.large[type="radio"]:before{
   height:30px;
    width: 30px;
}
.myinput.large[type="radio"]:after{
    top: -39px;
    width: 50px;
    height: 50px;

}
/* Custom checkbox */
.myinput.large.custom[type="radio"]:checked:after{
  /*background-image: url('tumbsup.png'), linear-gradient(135deg, #B1B6BE 0%,#FFF 100%);*/
  background-size: 100%;
    background-image: url('data:image/png;base64,'), linear-gradient(135deg, #B1B6BE 0%,#FFF 100%);

  /* background-image: url('data:image/png;base64,'), linear-gradient(135deg, #B1B6BE 0%,#FFF 100%);
 */

  border-color: #2196F3;
}



