}
.vue-slider-component .vue-slider {
	position: relative;
	display: block;
	border-radius: 15px;
	background-color: #ccc;
}
.vue-slider-component .vue-slider::after {
	content: '';
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 2;
}
.vue-slider-component .vue-slider-process {
	position: absolute;
	border-radius: 15px;
	background-color: #3498db;
	transition: all 0s;
	z-index: 1;
}
.vue-slider-component.vue-slider-horizontal .vue-slider-process {
	width: 0;
	height: 100%;
	top: 0;
	left: 0;
	will-change: width;
}
.vue-slider-component.vue-slider-vertical .vue-slider-process {
	width: 100%;
	height: 0;
	bottom: 0;
	left: 0;
	will-change: height;
}
.vue-slider-component.vue-slider-horizontal-reverse .vue-slider-process {
	width: 0;
	height: 100%;
	top: 0;
	right: 0;
}
.vue-slider-component.vue-slider-vertical-reverse .vue-slider-process {
	width: 100%;
	height: 0;
